:root {
  --text-primary: #fff;
  --bg-secondary: #111;
}

* {
  box-sizing: border-box;
  scrollbar-width: thin;
  margin: 0;
  padding: 0;
  font-family: Open Sans, sans-serif;
}

::selection {
  background-color: var(--bg-primary);
  color: var(--text-primary);
}

section {
  color: var(--text-primary);
  background-color: #000;
  padding: 5em;
}

section h2 {
  margin-bottom: 1em;
  font-size: 2.5em;
  position: relative;
}

section h2:after {
  content: "";
  background-color: var(--bg-primary);
  width: 2em;
  height: .1em;
  position: absolute;
  bottom: -.2em;
  left: 0;
}

.intro {
  cursor: none;
  background: linear-gradient(to right, #0007, #0000);
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100vh;
  display: flex;
}

.intro canvas {
  z-index: -1;
  background-color: #000;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.intro:after {
  content: "";
  z-index: 2;
  background: linear-gradient(to top, #000, #0000);
  width: 100%;
  height: 3em;
  position: absolute;
  bottom: 0;
  left: 0;
}

.intro h1 {
  -webkit-user-select: none;
  user-select: none;
  font-size: 3em;
  font-weight: lighter;
}

.intro h1 > span {
  display: block;
}

.intro h1 > span:last-child {
  font-size: .7em;
}

.intro a, form button {
  border: 1px solid var(--bg-primary);
  background-color: var(--bg-primary);
  color: var(--text-primary);
  cursor: pointer;
  z-index: 2;
  border-radius: .1em;
  outline: none;
  margin-top: 1em;
  padding: .5em 1.5em;
  font-size: 1.2em;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.skills__list, .projects__list {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.skills__list div h3 {
  margin-bottom: .5em;
  font-size: 1.5em;
}

.skills__list div ul {
  margin-bottom: 2em;
  list-style: lower-greek;
}

.skills__list div ul li {
  margin-bottom: .7em;
  margin-left: 1.5em;
  font-size: 1.2em;
}

.experience__list {
  flex-wrap: wrap;
  gap: 2em;
  display: flex;
}

.experience__list div {
  flex: 1;
}

.experience__list div h3 {
  margin-bottom: .3em;
  font-size: 1.5em;
}

.experience__list div h4 {
  margin-bottom: 1em;
  font-size: 1.2em;
}

.experience__list div p {
  color: #777;
}

.projects__list > div {
  width: 15em;
  margin-bottom: 2em;
}

.projects__list > div > div {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 1.2em;
  font-size: 1.5em;
  display: flex;
}

.projects__list > div > div > a {
  color: var(--text-primary);
}

.projects__list div img {
  border-radius: .1em;
  width: 100%;
  height: 15em;
  transition: transform .5s;
  box-shadow: 0 0 1em .2em #fff1;
}

.projects__list div img:hover {
  transform: scale(1.1);
}

.projects__list div p {
  color: #777;
  margin-top: .7em;
  font-weight: normal;
}

.about__contact {
  flex-wrap: wrap;
  display: flex;
}

.about__contact > div {
  width: 50%;
}

.about__contact > div > p {
  padding-bottom: 2em;
  padding-right: 2em;
  font-size: 1.25em;
}

form {
  flex-direction: column;
  display: flex;
}

form div {
  flex-wrap: wrap;
  flex: 1;
  display: flex;
}

form label {
  flex-direction: column;
  flex: 1;
  margin-bottom: 1em;
  font-size: 1.1em;
  display: flex;
}

form label input, form label textarea {
  background-color: var(--bg-secondary);
  color: var(--text-primary);
  resize: none;
  border: none;
  outline: none;
  margin-top: .6em;
  margin-right: 2em;
  padding: .4em .5em;
  font-size: 1em;
}

form label input:focus, form label textarea:focus {
  border: none;
  outline: none;
}

form button {
  width: max-content;
  margin-top: .5em;
  margin-right: 1em;
  padding: .3em 1.5em;
}

form button:disabled {
  cursor: not-allowed;
  opacity: .7;
}

form div .alert {
  background-color: #0f07;
  border-radius: .5em;
  margin-top: .5em;
  padding: .5em 1.5em;
  display: none;
}

form div .alert.visible {
  display: unset;
}

footer {
  background-color: var(--bg-secondary);
  color: var(--text-primary);
  justify-content: center;
  align-items: center;
  padding: 2em;
  display: flex;
}

footer div {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.social__links {
  margin-bottom: .5em;
  list-style-type: none;
  display: flex;
}

.social__links li {
  margin-left: .8em;
  font-size: 1.5em;
}

.social__links li a {
  color: var(--text-primary);
}

footer p span {
  color: var(--bg-primary);
}

@media (width <= 1000px) {
  .intro {
    background: #0005;
  }

  section {
    padding: 2.5em;
    font-size: 15px;
  }

  .intro {
    font-size: 12px;
  }

  .about__contact {
    flex-wrap: wrap;
  }

  .about__contact > div, .about__contact > div > form > div > label {
    width: 100%;
  }
}

